.About {
	.introduce {
		.box {
			width: 100%;
			height: 600px;
			background: #00976e;
			margin-bottom: 100px;
			overflow: hidden;
			.pic {
				float: left;
				width: 50%;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			.text1 {
				height: 600px;
				text-align: center;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				> h1 {
					font: 35px/1 'airal';
				}
				> p {
					font: 15px/1.2 'airal';
					margin-bottom: 0;
					width: 600px;
				}
			}
		}
	}

	h1 {
		padding: 0 30%;
		text-align: center;
		font: 35px/1 'airal';
		margin-bottom: 20px;
		span {
			font: bold 13px/1 'arial';
			display: block;
			letter-spacing: 2px;
			margin-bottom: 20px;
		}
	}
	p {
		padding: 0 26%;
		text-align: center;
		margin-bottom: 100px;
	}

	.introduce2 {
		width: 900px;
		margin: 0 auto;
		.mission {
			width: 100%;
			margin-bottom: 50px;
			.wrap {
				&::after {
					display: block;
					content: '';
					clear: both;
				}
				.pic {
					width: 50%;
					float: left;
					img {
						width: 350px;
						height: 350px;
						object-fit: cover;
						border-radius: 50%;
					}
				}
				.text2 {
					width: 50%;
					height: 350px;
					float: left;
					display: flex;
					flex-direction: column;
					justify-content: center;
					span {
						display: block;
						text-align: center;
						font: bold 15px/1 'arial';
						margin-bottom: 20px;
						letter-spacing: 2px;
					}
					h2 {
						font: 35px/ 1 'arial';
						text-align: center;
					}
				}
			}
		}

		.vision {
			width: 100%;
			margin-bottom: 50px;
			.wrap {
				&::after {
					display: block;
					content: '';
					clear: both;
				}
				.text3 {
					width: 50%;
					height: 350px;
					float: left;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: left;
					span {
						display: block;
						text-align: center;
						font: bold 15px/1 'arial';
						margin-bottom: 20px;
						letter-spacing: 2px;
					}
					h2 {
						font: 35px/ 1 'arial';
						text-align: center;
					}
				}
				.pic {
					width: 50%;
					float: left;
					img {
						float: right;
						width: 350px;
						height: 350px;
						object-fit: cover;
						border-radius: 50%;
					}
				}
			}
		}
	}
}
